.tabelaDeTecnologias {
    height: 600px;
    column-count: 2;
}

.tabelaDeTecnologias li {
    padding-top: 10px;
    display: list-item;
    list-style-type: disc;
}

.tabelaDeTecnologias img {
    width: 6%;
    padding-left: 10px;
    display: inline-table;
}

#seconddiv0 {
    max-height: 0%;
    transition: 1.0s;
    overflow: hidden;
    display: block
   }
   
#seconddiv0.divAberta0 {
    max-height: fit-content;
    transition: 1.0s;
    background-color: rgb(190, 190, 190);
    padding-right: 30px;
   }

#seconddiv0.divAberta0 > #texto0 {
    display: block;
}

#texto0 {
    margin-top: 5px;
    margin-left: 5px;
    font-family: 'Courier New', Courier, monospace;
    font-style: italic;
    font-size: medium;
    display: none;
}

#seconddiv1 {
    max-height: 0%;
    transition: 1.0s;
    overflow: hidden;
    display: block
   }

#seconddiv1.divAberta1 {
    max-height: 100%;
    transition: 1.0s;
    background-color: rgb(190, 190, 190);
   }

#seconddiv1.divAberta1 > #texto1 {
    display: block;
}

#texto1 {
    margin-top: 5px;
    margin-left: 5px;
    font-family: 'Courier New', Courier, monospace;
    font-style: italic;
    font-size: medium;
    display: none;
}
